.faq {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 198px;
  padding-left: 198px;
  margin-bottom: 108px;

  h2 {
    font-family: $extra-font;
    font-weight: 700;
    font-size: 34px;
    line-height: 44px;
    color: $layout-black-lighter;
    margin: 0;
    margin-bottom: 60px;
    text-align: center;
  }

  @media (max-width: $desktop-width) {
    padding-right: 45px;
    padding-left: 45px;
    margin-bottom: 80px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 70px;
    padding-right: 15px;
    padding-left: 15px;
  }
}
